        "id": 40794,
        "title": "ICASI integrates into FIRST PSIRT SIG bolstering the incident response and security team industry",
        "summary": "**June 1<sup>st</sup>, 2021** – ICASI – the Industry Consortium for Advancement of Security on the Internet was officially integrated into the Forum of Incident Response and Security Teams (FIRST) on May 28, 2021. Established in 2008, ICASI’s purpose was to strengthen the global security landscape by driving excellence and innovation in security response practices; facilitating collaboration among members to analyze, mitigate, and resolve multi-stakeholder, global security challenges. This role will continue but as part of the existing FIRST PSIRT SIG, expand and improve the community’s ability to respond to vulnerabilities across multiple vendors. Founded in 1990, FIRST is the global leader in incident response.",
        "link": "https://www.first.org/newsroom/releases/20210601",
        "published": "Tue, 01 Jun 2021 13:00:00 GMT"
