        "id": 40739,
        "title": "2019 Episode 9:  Seeing Clearly and Communicating Effectively to Address Event Overload– Thomas Fisher, FVT SecOps Consulting",
        "summary": "Is content king? Fisher argues data alone can lead us astray, instead, it is the story we should focus on. With a presentation loaded with artwork and visuals, Fisher hopes to teach statistic savvy security responders to see the bigger picture. What patterns appear when we take a step back? What narrative does the evidence summon? Question your answers and dive into this discussion with Chris and Martin.",
        "link": "https://media.first.org/podcasts/FIRST2019-Fischer.mp3",
